<template>
  <header class="navigation__bar" :class="{ transparent, scrolled }">
    <div class="navigation__bar-maxwidth">
      <router-link
        class="navigation__bar-logowrapper"
        :to="'/'"
        aria-label="Ham Express logo"
      >
      </router-link>
      <nav
        v-if="showNavigationItems"
        class="navigation__items"
        v-bind:class="{ active: navigationItemsVisible }"
      >
        <router-link
          v-for="item in navigationItems"
          :key="item.path"
          :to="item.path"
          class="navigation__item-link"
        >
          {{ item.name }}
        </router-link>
      </nav>
      <div class="navigation__bar-buttons">
        <div
          class="hamburger"
          v-bind:class="{ active: navigationItemsVisible }"
          @click="toggleNavigation"
          v-if="showNavigationItems"
        >
          <div class="hamburger__item"></div>
          <div class="hamburger__item"></div>
          <div class="hamburger__item"></div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { ref, onMounted, onUnmounted } from "vue";

export default {
  props: {
    showNavigationItems: Boolean,
    transparent: Boolean,
  },
  setup() {
    /* const errors = ref([]); */
    const scrolled = ref(false);
    const navigationItemsVisible = ref(false);
    const navigationItems = [
      {
        name: "Home",
        path: "/",
      },
      {
        name: "Zeilboot",
        path: "/zeilboot",
      },
      {
        name: "Verhuur",
        path: "/verhuur",
      },
      {
        name: "Locatie",
        path: "/locatie",
      },
      {
        name: "Contact",
        path: "/contact",
      },
    ];

    function toggleNavigation() {
      this.navigationItemsVisible = !this.navigationItemsVisible;
    }

    function handleScrollEvent() {
      if (window.scrollY > 80) return scrolled.value = true;

      return scrolled.value = false;
    }

    onMounted(() => {
      window.addEventListener("scroll", handleScrollEvent);
    })

    onUnmounted(() => {
      window.removeEventListener("scroll", handleScrollEvent);
    })

    return {
      navigationItemsVisible,
      toggleNavigation,
      navigationItems,
      scrolled,
    };
  },
};
</script>
