      >
        <span v-if="text" class="hero__content-text">{{ text }}</span>
      </div>
    </div>
    <div v-if="pageTitle" class="hero__content-pagetitle-container">
      <span class="hero__content-pagetitle">{{ pageTitle}}</span>
    </div>
    <ResponsiveImg class="hero__img" :imgId="imgId" :imgType="isSmall ? 'heroImgSmall' : 'heroImg'" :imgAlt="imgAlt" />
  </div>
</template>

<script>
import ResponsiveImg from "@/components/ResponsiveImg.vue";

export default {
  props: {
    imgId: String,
    imgAlt: String,
    contentPosition: String,
    text: String,
    pageTitle: String,
    isSmall: Boolean
  },
  setup() {
    return {
      ResponsiveImg   
    }
  }
