<template>
  <div class="minheight__wrapper">
    <NavigationBar :showNavigationItems="true" />
    <div id="map" class="map"></div>
    <div class="page__content-container">
      <h1 class="page__content-title">Locatie</h1>
    </div>
    <Footer />
  </div>
</template>

<script>
import { Loader } from "@googlemaps/js-api-loader";

import NavigationBar from "@/components/NavigationBar.vue";
import HeaderImg from "@/components/HeaderImg.vue";
import Footer from "@/components/Footer.vue";
import { onMounted } from "vue";

export default {
  setup() {
    let map;
    const houseCoordinates = { lat: 52.94968, lng: 5.623 };

    const loader = new Loader({
      apiKey: "AIzaSyAIgCdkYiD3tPSv4le4WRP7smIZSYMQFsE",
      version: "weekly",
    });

    onMounted(() => {
      loader.load().then(() => {
        map = new google.maps.Map(document.getElementById("map"), {
          center: houseCoordinates,
          zoom: 13,
        });

        new google.maps.Marker({
          position: houseCoordinates,
          map,
        });
      });
    });

    return {
      NavigationBar,
      HeaderImg,
      Footer,
    };
  },
};
</script>
